import React, { useCallback, useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";

import {
  PasswordInput,
  Button,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { validateEmail } from "../../utils/validation";

import styles from "../common.module.css";
import { loginUser } from "../../services/actions/auth";

export function LoginPage() {
  const dispatch = useDispatch();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const isEmailValid = useCallback(() => !!validateEmail(email), [email]);
  const isFormValid = useCallback(
    () => isEmailValid() && password !== "",
    [isEmailValid, password]
  );

  const login = (e) => {
    e.preventDefault();
    dispatch(loginUser({ email, password }));
  };

  return (
    <div className={styles.container}>
      <form className={`${styles.form} `} onSubmit={login}>
        <div className="text text_type_main-medium mb-6"></div>
        <div className={`${styles.input} mb-6`}>
          <Input
            size="default"
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder=" e-mail"
            error={email !== "" && !isEmailValid()}
            errorText=" email"
          />
        </div>
        <div className={`${styles.input} mb-6`}>
          <PasswordInput
            onChange={(e) => setPassword(e.target.value)}
            value={password}
            name="password"
          />
        </div>
        <div className="mb-20">
          <Button type="primary" size="large" disabled={!isFormValid()}>
            
          </Button>
        </div>
      </form>
      <p className="text text_type_main-default text_color_inactive mb-4">
         -  ?
        <Link
          to="/register"
          className={`${styles.link} ml-2 text text_type_main-default`}
        >
          
        </Link>
      </p>
      <p className="text text_type_main-default text_color_inactive ">
         ?
        <Link
          to="/forgot-password"
          className={`${styles.link} ml-2 text text_type_main-default`}
        >
           
        </Link>
      </p>
    </div>
  );
}
